import Rectangle from '../../Engine/Geometry/Rectangle';
import BaseScreen from './BaseScreen';
import Credits from './Credits';
import HighScores from './HighScores';
import Controls from './Controls';

export default class Menu extends BaseScreen {
    private screens: BaseScreen[] = [];

    constructor(onNewGame: () => void) {
        super('Frogger');

        let curY = 200;

        this.buttons.push({
            bounds: new Rectangle(this.buttonX, curY, this.buttonW, 100),
            text: { content: 'New Game', font: '64px arial' },
            onSelect: onNewGame,
        });

        const screenTypes = [HighScores, Credits, Controls];

        for (let i = 0; i < screenTypes.length; i++) {
            const screen = new screenTypes[i](this.returnToMenu);
            this.screens.push(screen);
            this.buttons.push({
                bounds: new Rectangle(
                    this.buttonX,
                    (curY += 200),
                    this.buttonW,
                    100,
                ),
                text: { content: screen.title, font: '64px arial' },
                onSelect: () => this.transitionToScreen(i),
            });
        }

        this.createRenderersFromButtons();
    }

    private transitionToScreen = (index: number) => {
        if (this.screens[index].refresh) {
            this.screens[index].refresh();
        }

        this.internalRender = this.screens[index].render;
        this.internalMouseClick = this.screens[index].mouseClick;
    };

    private returnToMenu = () => {
        this.internalRender = this.renderButtons;
        this.resetMouseClick();
    };
}
