import GamePlay from './Game/GamePlay';

const canvas = document.getElementById('game-canvas') as HTMLCanvasElement;

const gamePlay = new GamePlay(canvas);
gamePlay.play();

const handleResize = (ev: any) => {
    const { innerWidth, innerHeight } = ev.target as Window;

    const minSize = (Math.min(innerWidth, innerHeight) - 16).toString();
    canvas.style.width = minSize;
    canvas.style.height = minSize;
};

window.addEventListener('resize', handleResize);
handleResize({ target: window });
