import Vec2 from '../Math/Vec2';

export default class Rectangle {
    constructor(
        public x: number,
        public y: number,
        public w: number,
        public h: number,
    ) {}

    public inBounds(pos: Vec2) {
        return (
            pos.x >= this.x &&
            pos.x <= this.x + this.w &&
            pos.y >= this.y &&
            pos.y <= this.y + this.h
        );
    }
}

export function copy(a: Rectangle) {
    return new Rectangle(a.x, a.y, a.w, a.h);
}
