import Path from './Path';
import Vec2 from '../../Engine/Math/Vec2';
import { CanvasRenderer } from '../../Engine/Renderers/CanvasRenderer';
import SpriteSheet from '../../Engine/Graphics/SpriteSheet';
import Rectangle, { copy } from '../../Engine/Geometry/Rectangle';
import settings from '../../../settings';
import Animation from '../../Engine/Animation/Animation';

export interface Props {
    readonly speed: number;
    readonly type: 'car' | 'log' | 'tur';
    readonly center: Vec2;
    readonly ss: SpriteSheet;
    readonly goingLeft: boolean;
    readonly asset: string;
}

interface AnimProps {
    readonly frames: string[];
    readonly loopTime: number;
}

interface State {
    speed: number;
    path: Path;
    center: Vec2;
    bounds: Rectangle;
}

export default class PathedObject {
    protected readonly state: State;
    protected anim?: Animation;
    protected curFrame: string;
    protected rotation: number;
    protected _canStand: boolean = false;

    constructor(protected readonly props: Props, animProps?: AnimProps) {
        this.state = {
            speed: props.speed,
            path: new Path(false, undefined, true),
            center: props.center,
            bounds: copy(this.props.ss.getBounds(this.props.asset)),
        };

        if (animProps) {
            this.anim = new Animation(
                animProps.frames,
                animProps.loopTime,
                true,
            );
            this.curFrame = animProps.frames[0];
        } else {
            this.curFrame = this.props.asset;
        }

        this.rotation = props.goingLeft ? Math.PI : 0;

        const { w } = this.props.ss.getBounds(this.props.asset);

        const halfWidth = w / 2;
        if (!props.goingLeft) {
            this.state.path.addPoint({ x: -halfWidth, y: props.center.y });
            this.state.path.addPoint({
                x: 1024 + halfWidth,
                y: props.center.y,
            });
        } else {
            this.state.path.addPoint({
                x: 1024 + halfWidth,
                y: props.center.y,
            });
            this.state.path.addPoint({ x: -halfWidth, y: props.center.y });
        }

        this.state.path.travel(props.center.x);
    }

    get speed() {
        return this.state.speed;
    }

    get goingLeft() {
        return this.props.goingLeft;
    }

    set speed(newSpeed: number) {
        this.state.speed = newSpeed;
    }

    get type() {
        return this.anim ? 'alligator' : this.props.type;
    }

    public update = (elapsedTime: number) => {
        const newPoint = this.state.path.travel(this.state.speed * elapsedTime);

        this.state.center = newPoint;
        this.state.bounds.x = newPoint.x - this.state.bounds.w / 2;
        this.state.bounds.y = newPoint.y - this.state.bounds.h / 2;

        if (this.anim) {
            this.curFrame = this.anim.watch(elapsedTime);
            if (/chomp/.test(this.curFrame)) {
                this.rotation = -Math.PI / 2;
                this._canStand = false;
            } else {
                this.rotation = 0;
                this._canStand = true;
            }
        }
    };

    public render = (cr: CanvasRenderer) => {
        cr.drawSubTexture(
            this.props.ss,
            this.curFrame,
            this.state.center,
            this.rotation,
            true,
        );

        // // center
        if (settings.showCollisionShapes) {
            cr.drawCircle(
                this.state.center.x,
                this.state.center.y,
                10,
                '#fff',
                '#f00',
            );

            // collision box
            cr.drawRectangle(
                this.state.bounds.x,
                this.state.bounds.y,
                this.state.bounds.w,
                this.state.bounds.h,
                0,
                'rgba(00, 104, 255, 0.5)',
            );
        }
    };

    public testPoint = (a: Vec2) => {
        // const { x, y } = this.state.center;
        // const { w, h } = this.state.bounds;

        // const collisionBox = new Rectangle(x - w / 2, y - h / 2, 2, h)

        return this.state.bounds.inBounds(a);
    };

    get canStandOn() {
        return this._canStand;
    }
}
