import Vec2, { expand } from '../../Engine/Math/Vec2';
import { CanvasRenderer } from '../../Engine/Renderers/CanvasRenderer';
import { Circle } from '../../Engine/Math/circle';
import SpriteSheet from '../../Engine/Graphics/SpriteSheet';
import Path from './Path';
import Animation from '../../Engine/Animation/Animation';
import settings from '../../../settings';
import { AudioSystem } from '../../Engine/Systems/AudioSystem';

interface Props {
    center: Vec2;
    collisionRadius: number;
    width: number;
    spriteSheet: SpriteSheet;
    /**
     * Number to increase a x or y value by when moving
     */
    delta: number;
}

export default class Player {
    private _center: Vec2;
    private _inMove: boolean = false;
    private _animPath: Path;
    private _rotation: number = Math.PI;

    /**
     * Change in x when on a log or turtle
     */
    private _speedX = 0;

    private pixels: number = 100;
    /**
     * Time in milliseconds for player travel/animations
     */
    private readonly milli: number = 500;
    private speed: number = this.pixels / this.milli;

    private _currentFrame: string = 'frogger-idle';
    private _anim = new Animation(
        [
            'frogger-1',
            'frogger-2',
            'frogger-3',
            'frogger-4',
            'frogger-3',
            'frogger-2',
            'frogger-1',
            'frogger-idle',
        ],
        this.milli,
    );

    private internalUpdate: (elapsedTime: number) => void;

    constructor(private props: Props, private audioSystem: AudioSystem) {
        this._center = props.center;
        this.internalUpdate = this.handleBeingMoved;
        this._animPath = new Path(false, this.handleAnimationDone);
    }

    public readonly update = (elapsedTime: number) => {
        this.internalUpdate(elapsedTime);
    };

    public readonly render = (cr: CanvasRenderer) => {
        const { x, y } = this._center;

        cr.drawSubTexture(
            this.props.spriteSheet,
            this._currentFrame,
            // -60 will line up the sprite in the center vertically. This is because
            // we're drawing a sprite bigger than it actually is.
            { x, y },
            this._rotation,
            true,
        );

        // draw circle showing center of frogger
        if (settings.showCollisionShapes) {
            cr.drawCircle(x, y, 10);
        }

        // draw larger circle showing collision radius
    };

    public moveLeft = () => {
        if (this._inMove) {
            return;
        }

        if (this._center.x <= 1024 * (1 / 13)) {
            return;
        }

        this.audioSystem.addAudio('audio/sound-frogger-hop.mp3', 0, false, 0.5);
        this.initializeMove(Math.PI / 2, (a: Vec2) => {
            return { x: a.x - this.props.delta, y: a.y };
        });
    };

    public moveRight = () => {
        if (this._inMove) {
            return;
        }

        if (this._center.x >= 1024 * (12 / 13)) {
            return;
        }

        this.audioSystem.addAudio('audio/sound-frogger-hop.mp3', 0, false, 0.5);
        this.initializeMove(-Math.PI / 2, (a: Vec2) => {
            return { x: a.x + this.props.delta, y: a.y };
        });
    };

    public moveUp = () => {
        if (this._inMove) {
            return;
        }

        this._speedX = 0;

        this.audioSystem.addAudio('audio/sound-frogger-hop.mp3', 0, false, 0.5);
        this.initializeMove(Math.PI, (a: Vec2) => {
            return { x: a.x, y: a.y - this.props.delta };
        });
    };

    public moveDown = () => {
        if (this._inMove) {
            return;
        }

        if (this._center.y > (1024 * 12) / 13) {
            return;
        }

        this._speedX = 0;

        this.audioSystem.addAudio('audio/sound-frogger-hop.mp3', 0, false, 0.5);
        this.initializeMove(0, (a: Vec2) => {
            return { x: a.x, y: a.y + this.props.delta };
        });
    };

    public get collisionShape(): Circle {
        return {
            center: this._center,
            radius: this.props.collisionRadius,
        };
    }

    public get center() {
        return this._center;
    }

    private initializeMove = (rotation: number, modify: (a: Vec2) => Vec2) => {
        this._inMove = true;
        this._rotation = rotation;
        this._anim.reset();

        this._animPath.clear();

        this._animPath.addPoint(expand(this._center));
        this._animPath.addPoint(modify(this._center));

        this.internalUpdate = this.handleTravelStep;
    };

    private handleAnimationDone = () => {
        this._currentFrame = 'frogger-1';
        this._inMove = false;
        this.internalUpdate = this.handleBeingMoved;
    };

    private handleTravelStep = (elapsedTime: number) => {
        if (!this._inMove) {
            return;
        }

        const newPoint = this._animPath.travel(this.speed * elapsedTime);
        this._center.x = newPoint.x;
        this.handleBeingMoved(elapsedTime);
        this._center.y = newPoint.y;

        this._currentFrame = this._anim.watch(elapsedTime);
    };

    get idle() {
        return this._currentFrame === 'frogger-idle' && !this._inMove;
    }

    set speedX(newDeltaX: number) {
        this._speedX = newDeltaX;
    }

    private handleBeingMoved = (elapsedTime: number) => {
        this._center.x += this._speedX * elapsedTime;
    };
}
