export default class ScoringSystem {
    private _visitedArray: boolean[] = [];

    private _currentRow: number = 0;

    private _score: number = 0;

    constructor(private numRows: number = 13) {
        this.resetForNewFrog();
    }

    public gotFly() {
        this._score += 200;
    }

    public landOnPad() {
        this._score += 50;
    }

    public beatLevel() {
        this._score += 1000;
    }

    public moveUp() {
        this._currentRow++;

        if (!this._visitedArray[this._currentRow]) {
            this._visitedArray[this._currentRow] = true;
            this._score += 10;
        }
    }

    public moveDown() {
        this._currentRow--;
    }

    public resetForNewFrog() {
        for (let i = 0; i < this.numRows; i++) {
            this._visitedArray[i] = false;
        }
        this._currentRow = 0;
    }

    /**
     * Scores left over time
     * @param time Time in ms
     */
    public accountForTime(time: number) {
        const scoreTime = Math.round(time / 100);
        this._score += scoreTime;
    }

    get score() {
        return this._score;
    }

    public resetScore() {
        this._score = 0;
    }
}
