import Vec2 from '../Math/Vec2';
import { Texture } from '../Graphics/Texture';
import SpriteSheet from '../Graphics/SpriteSheet';
import Rectangle from '../Geometry/Rectangle';

type Point = { x: number; y: number };

/**
 * Renders things to a 2D HTML Canvas
 */
export class CanvasRenderer {
    private context: CanvasRenderingContext2D;

    constructor(canvas: HTMLCanvasElement) {
        this.context = canvas.getContext('2d');
    }

    public get middleX() {
        return this.width / 2;
    }

    public get width() {
        return this.context.canvas.width;
    }

    public clear() {
        const { width, height } = this.context.canvas;

        this.context.clearRect(0, 0, width, height + 100);
    }

    public drawRectangle(
        x: number,
        y: number,
        width: number,
        height: number,
        rotation: number,
        fillStyle: string | CanvasGradient,
        strokeStyle?: string,
    ) {
        this.context.fillStyle = fillStyle;
        this.context.lineWidth = 5;

        if (strokeStyle) {
            this.context.strokeStyle = strokeStyle;
        }

        this.context.save();
        this.context.translate(x, y);
        this.context.rotate(rotation);
        this.context.translate(-x, -y);

        if (strokeStyle) {
            this.context.stroke();
        }

        this.context.fillRect(x, y, width, height);

        this.context.restore();
    }

    public drawText(
        pos: Vec2,
        text: string,
        rotation: number = 0,
        center: boolean = false,
        fillStyle: string = '#000',
        font: string = '64px arial',
    ) {
        this.context.save();
        this.context.font = font;
        const width = this.context.measureText(text).width;
        this.context.restore();

        this.context.save();
        this.context.font = font;
        let height = this.context.measureText('m').width;
        this.context.restore();

        this.context.save();
        this.context.font = font;

        this.context.fillStyle = fillStyle;

        const x = center ? pos.x - width / 2 : pos.x;
        const y = center ? pos.y - width / 2 : pos.y;

        this.context.textBaseline = 'top';
        this.context.translate(x + width / 2, y + height / 2);
        this.context.rotate(rotation);
        this.context.translate(-(x + width / 2), -(y + height / 2));

        this.context.fillText(text, x, pos.y);

        this.context.restore();
    }

    public drawPoints(
        points: Point[],
        strokeStyle: string,
        fillStyle: string,
        closePath: boolean = false,
    ) {
        this.context.beginPath();
        this.context.strokeStyle = strokeStyle;
        this.context.fillStyle = fillStyle;
        this.context.lineWidth = 3;

        this.context.moveTo(points[0].x, points[0].y);

        for (let i = 1; i < points.length; i++) {
            this.context.lineTo(points[i].x, points[i].y);
        }

        if (closePath) {
            this.context.closePath();
        }
        this.context.fill();
        this.context.stroke();
    }

    public getTextWidth(text: string, font: string) {
        this.context.save();
        this.context.font = font;
        const width = this.context.measureText(text).width;
        this.context.restore();
        return width;
    }

    public getFontHeight(font: string) {
        this.context.save();
        this.context.font = font;
        const height = this.context.measureText('m').width;
        this.context.restore();
        return height;
    }

    public drawTexture(texture: Texture) {
        if (texture.ready) {
            this.context.save();
            this.context.translate(texture.center.x, texture.center.y);
            this.context.rotate(texture.rotation);
            this.context.translate(-texture.center.x, -texture.center.y);
            this.context.drawImage(
                texture.image,
                texture.center.x - texture.width / 2,
                texture.center.y - texture.height / 2,
                texture.width,
                texture.height,
            );
            this.context.restore();
        }
    }

    /**
     * Draws the texure from the bounds stored in the key.
     * @param spriteSheet Sprite sheet to pull dat from
     * @param key Key to get bounds
     * @param center Center of the texture
     * @param centered Is the texured centered on x & y?
     * @param centered Is the
     */
    public drawSubTexture(
        spriteSheet: SpriteSheet,
        key: string,
        center: Vec2,
        rotation: number,
        centered: boolean = false,
    ) {
        if (spriteSheet.ready) {
            const bounds = spriteSheet.getBounds(key);

            const x = centered ? center.x - bounds.w / 2 : center.x;
            const y = centered ? center.y - bounds.h / 2 : center.y;

            this.context.save();
            this.context.translate(center.x, center.y);
            this.context.rotate(rotation);
            this.context.translate(-center.x, -center.y);
            this.context.drawImage(
                spriteSheet.image,
                bounds.x,
                bounds.y,
                bounds.w,
                bounds.h,
                x,
                y,
                bounds.w + 0.5,
                bounds.h + 0.5,
            );
            this.context.restore();
        }
    }

    /**
     * Draws the texure in the clipping area
     * @param spriteSheet Sprite sheet to pull dat from
     * @param key Key to get bounds
     * @param center Center of the texture
     * @param centered Is the texured centered on x & y?
     * @param clippingArea Area to clip
     */
    public drawSubTextureInClip(
        spriteSheet: SpriteSheet,
        key: string,
        center: Vec2,
        centered: boolean = false,
        clippingArea: Rectangle,
    ) {
        if (spriteSheet.ready) {
            const bounds = spriteSheet.getBounds(key);

            const x = centered ? center.x - bounds.w / 2 : center.x;
            const y = centered ? center.y - bounds.h / 2 : center.y;

            this.context.save();

            this.context.rect(
                clippingArea.x,
                clippingArea.y,
                clippingArea.w,
                clippingArea.h,
            );
            this.context.clip();

            this.context.drawImage(
                spriteSheet.image,
                bounds.x,
                bounds.y,
                bounds.w,
                bounds.h,
                x,
                y,
                bounds.w + 0.5,
                bounds.h + 0.5,
            );
            this.context.restore();
        }
    }

    public createGradient(pt1: Vec2, pt2: Vec2) {
        return this.context.createLinearGradient(pt1.x, pt1.y, pt2.x, pt2.y);
    }

    public drawStrokeRect(
        x: number,
        y: number,
        w: number,
        h: number,
        style: string,
    ) {
        this.context.strokeStyle = style;
        this.context.lineWidth = 3;

        this.context.save();

        this.context.strokeRect(x, y, w, h);

        this.context.restore();
    }

    public drawCircle(
        x: number,
        y: number,
        radius: number,
        fillStyle = '#000',
        strokeStyle = '#fff',
    ) {
        this.context.save();
        this.context.beginPath();
        this.context.arc(x, y, radius, 0, 2 * Math.PI, false);
        this.context.fillStyle = fillStyle;
        this.context.fill();
        this.context.lineWidth = 5;
        this.context.strokeStyle = strokeStyle;
        this.context.stroke();
        this.context.restore();
    }
}
