import Text from '../Graphics/Text';
import { CanvasRenderer } from '../../Engine/Renderers/CanvasRenderer';

const TextRenderer = (text: Text) => (cr: CanvasRenderer) => {
    cr.drawText(
        text.pos,
        text.content,
        text.rotation ?? 0,
        text.centered ?? false,
        text.fill ?? '#000',
        text.font ?? '64px arial',
    );
};

export default TextRenderer;
