export default class Animation {
    private _elapsedTime = 0;
    private _curFrame = 1;
    private _timePerFrame = 0;

    constructor(
        private frames: string[],
        animTime: number,
        private loop: boolean = false,
    ) {
        this._timePerFrame = animTime / (frames.length + 1);
    }

    public readonly reset = () => {
        this._elapsedTime = 0;
        this._curFrame = 1;
    };

    public readonly watch = (elapsedTime: number) => {
        this._elapsedTime += elapsedTime;

        if (this._elapsedTime >= this._timePerFrame * this._curFrame) {
            if (this.loop) {
                if (this._curFrame >= this.frames.length) {
                    this._elapsedTime = 0;
                    this._curFrame = 1;
                } else {
                    this._curFrame += 1;
                }
            } else {
                this._curFrame +=
                    this._curFrame < this.frames.length
                        ? 1
                        : this.frames.length;
            }
        }

        return this.frames[this._curFrame - 1];
    };

    get timePerFrame() {
        return this._timePerFrame;
    }

    get currentFrame() {
        return this.frames[this._curFrame - 1];
    }
}
