import Rectangle from '../../Engine/Geometry/Rectangle';
import BaseScreen from './BaseScreen';
import ControlsManager from '../../Engine/Managers/ControlsManager';
import Button from '../UI/Button';
import { CanvasRenderer } from '../../Engine/Renderers/CanvasRenderer';

export default class Controls extends BaseScreen {
    private controlsText: string[] = [];

    private inSelection: boolean = false;
    private selectedControlOptionIndex: number;

    constructor(private onBack: () => void) {
        super('Controls');

        let curY = 200;

        this.initializeControlsText();
        const controls = ControlsManager.getControls();

        const textW = this.buttonW + 300;
        const textX = this.getXFromW(textW);

        for (let i = 0; i < this.controlsText.length; i++) {
            const button = {
                bounds: new Rectangle(textX, (curY += 100), textW, 100),
                text: {
                    content: this.createTextContent(
                        this.controlsText[i],
                        controls[i],
                    ),
                    font: '64px arial',
                    fill: '#00aaff',
                },
                onSelect: () => {
                    if (!this.inSelection) {
                        button.text.content = this.createTextContent(
                            this.controlsText[i],
                            'Press a key...',
                        );
                        this.getKeyAndUpdateIndex(i);
                    }
                },
                style: { fill: '#fff' },
            } as Button;
            this.buttons.push(button);
        }

        this.createBackButton(curY, this.handleOnBack);

        this.createRenderersFromButtons();

        this.internalRender = (cr: CanvasRenderer) => {
            cr.drawText({ x: cr.middleX, y: 100 }, this.title, 0, true);
            cr.drawText(
                { x: cr.middleX, y: 200 },
                'Click on an option and press a key to set the control.',
                0,
                true,
                '#000',
                '32px arial',
            );
            for (let i = 0; i < this.renderers.length; i++) {
                this.renderers[i](cr);
            }
        };
    }

    private initializeControlsText = () => {
        this.controlsText = ['Move Left', 'Move Right', 'Move Up', 'Move Down'];
    };

    private resetControlsText = () => {
        const controls = ControlsManager.getControls();
        this.initializeControlsText();
        for (let i = 0; i < this.controlsText.length; i++) {
            this.buttons[i].text.content = this.createTextContent(
                this.controlsText[i],
                controls[i].toString(),
            );
        }
    };

    private handleOnBack = () => {
        this.stopListeningForKey();
        this.resetControlsText();
        this.onBack();
    };

    private createTextContent = (base: string, left: string) => {
        return `${base}: ${left !== ' ' ? left : 'Space'}`;
    };

    private getKeyAndUpdateIndex = (index: number) => {
        if (!this.inSelection) {
            this.startListeningForKey();
            this.selectedControlOptionIndex = index;
        }
    };

    private getNextKey = (ev: KeyboardEvent) => {
        this.stopListeningForKey();
        ControlsManager.updateControl(this.selectedControlOptionIndex, ev.key);

        this.resetControlsText();
    };

    private startListeningForKey = () => {
        this.inSelection = true;
        window.addEventListener('keydown', this.getNextKey);
    };

    private stopListeningForKey = () => {
        this.inSelection = false;
        window.removeEventListener('keydown', this.getNextKey);
    };
}
