export default class Vec2 {
    x: number;
    y: number;
}

export function distSqrd(a: Vec2, b: Vec2) {
    return Math.pow(a.x - b.x, 2) + Math.pow(a.y - b.y, 2);
}

export function dist(a: Vec2, b: Vec2) {
    return Math.sqrt(distSqrd(a, b));
}

export function diff(a: Vec2, b: Vec2) {
    return { x: b.x - a.x, y: b.y - a.y };
}

export function magSqrd(a: Vec2) {
    return Math.pow(a.x, 2) + Math.pow(a.y, 2);
}

export function mag(a: Vec2) {
    return Math.sqrt(magSqrd(a));
}

export function expand(a: Vec2) {
    return { x: a.x, y: a.y };
}
