// This is used to give a small performance optimization

import Vec2 from './Vec2';

// in generating gaussian random numbers.
let usePrevious: boolean = false;
let y2: number = 0;

export namespace random {
    export function nextGaussian(mean: number, stdDev: number) {
        let x1 = 0;
        let x2 = 0;
        let y1 = 0;
        let z = 0;

        if (usePrevious) {
            usePrevious = false;
            return mean + y2 * stdDev;
        }

        usePrevious = true;

        do {
            x1 = 2 * Math.random() - 1;
            x2 = 2 * Math.random() - 1;
            z = x1 * x1 + x2 * x2;
        } while (z >= 1);

        z = Math.sqrt((-2 * Math.log(z)) / z);
        y1 = x1 * z;
        y2 = x2 * z;

        return mean + y1 * stdDev;
    }

    export function nextRange(min: number, max: number) {
        let range = max - min;
        return Math.floor(Math.random() * range + min);
    }

    export function nextCircleVector(): Vec2 {
        let angle = Math.random() * 2 * Math.PI;
        return {
            x: Math.cos(angle),
            y: Math.sin(angle),
        };
    }

    export function nextAngleVector(maxAngle: number = 2 * Math.PI): Vec2 {
        let angle = Math.random() * maxAngle;
        return {
            x: Math.cos(angle),
            y: Math.sin(angle),
        };
    }

    export function nextAngleInCone(
        centerAngle: number,
        angleOfCone: number = Math.PI / 2,
    ): Vec2 {
        const generatingMean = angleOfCone / 2;
        const particleAngle =
            nextGaussian(generatingMean, angleOfCone / 4) + angleOfCone;
        const angle = centerAngle - generatingMean + particleAngle;
        return {
            x: Math.cos(angle),
            y: Math.sin(angle),
        };
    }

    /**
     * Returns true or false, 50% chance
     * @returns {boolean} True or false
     */
    export function coinFlip(): boolean {
        return Math.random() >= 0.5;
    }
}
