import Particle from '../Graphics/Particle';

export default abstract class BaseParticleSystem {
    protected _particles: Particle[] = [];

    abstract update: (elapsedTime: number) => void;

    protected removeDeadParticles = (elapsedTime: number) => {
        for (let i = this._particles.length - 1; i >= 0; i--) {
            this._particles[i].update(elapsedTime);

            if (!this._particles[i].alive) {
                this._particles.splice(i, 1);
                continue;
            }
        }
    };

    get particles(): Particle[] {
        return this._particles;
    }

    public clear() {
        this._particles.length = 0;
    }
}
