// type Controls = { moveLeft: string; moveRight: string; shoot: string };

export default class ControlsManager {
    private static storageKey: string = '@frogger-controls';

    private static defaultControls: string[] = [
        'ArrowLeft',
        'ArrowRight',
        'ArrowUp',
        'ArrowDown',
    ];

    public static clear() {
        localStorage.removeItem(ControlsManager.storageKey);
    }

    public static getControls(): string[] {
        const controls = localStorage[ControlsManager.storageKey];

        return controls ? JSON.parse(controls) : [...this.defaultControls];
    }

    private static setControls(newControls: string[]) {
        localStorage[ControlsManager.storageKey] = JSON.stringify(newControls);
    }

    public static updateControl(index: number, newKey: string) {
        const controls = localStorage[ControlsManager.storageKey];

        let newControls = [...this.defaultControls];
        if (controls) {
            newControls = JSON.parse(controls);
        }

        if (index < 0 || index >= newControls.length) {
            return;
        }
        newControls[index] = newKey;

        ControlsManager.setControls(newControls);
    }
}
