import Rectangle from '../../Engine/Geometry/Rectangle';
import Vec2 from '../../Engine/Math/Vec2';
import Path from './Path';

export default class HomePad {
    private readonly _bounds: Rectangle;
    public hasFrog: boolean = false;
    public hasFly: boolean = false;
    public hasAligator: boolean = false;
    public path: Path;
    public speed: number = 0;
    private _curPos: Vec2;

    private readonly timeSpan: number = 3000;
    private elapsedTime: number = 0;

    constructor(x: number, y: number, w: number, h: number) {
        this._bounds = new Rectangle(x - w / 2, y - h / 2, w, h);
    }

    public inBounds(a: Vec2) {
        return this._bounds.inBounds(a);
    }

    get bounds() {
        return this._bounds;
    }

    public readonly update = (elapsedTime: number) => {
        // if has fly, keep it there for 2 seconds
        if (this.hasFly) {
            this.elapsedTime += elapsedTime;

            if (this.elapsedTime >= this.timeSpan) {
                this.elapsedTime = 0;
                this.hasFly = false;
            }
        }

        // if there is a aligator, update it
        if (this.hasAligator) {
            this._curPos = this.path.travel(this.speed * elapsedTime);
        }
    };

    public readonly putFly = () => {
        console.log('FLY');
        this.hasFly = true;
        this.elapsedTime = 0;
    };

    public readonly putAligator = () => {
        console.log('ALIGATOR');
        this.hasAligator = true;
        this.elapsedTime = 0;
        this.path = new Path(false, () => {
            this.hasAligator = false;
        });
        const y = this._bounds.y + 30;
        this.path.addPoint({ x: this._bounds.x - 10, y });
        this.path.addPoint({
            x: this._bounds.x + this._bounds.w / 2 - 10,
            y,
        });
        this.path.addPoint({ x: this._bounds.x - 10, y });
        this.speed = this.path.length / this.timeSpan;
    };

    get aligatorPos() {
        return this._curPos;
    }
}
