import Rectangle from '../../Engine/Geometry/Rectangle';
import BaseScreen from './BaseScreen';
import HighScoreManager from '../../Engine/Managers/HighScoreManager';
import TextRenderer from '../../Engine/Renderers/TextRenderer';
import Text from '../../Engine/Graphics/Text';

export default class HighScores extends BaseScreen {
    private highScoreTextPairs: { username: Text; score: Text }[] = [];

    constructor(private onBack: () => void) {
        super('High Scores');
        this.createScreen();
    }

    private createScreen = () => {
        this.renderers.length = 0;
        this.buttons.length = 0;

        let curY = 250;

        const usernameX = 175;
        const scoreX = 700;

        const highScores = HighScoreManager.getHighScores().reverse();

        for (let i = 0; i < highScores.length; i++) {
            const { username, score } = highScores[i];
            const usernameText: Text = {
                content: username.toString(),
                pos: { x: usernameX, y: curY },
            };
            this.renderers.push(TextRenderer(usernameText));

            const scoreText: Text = {
                content: score.toString(),
                pos: { x: scoreX, y: curY },
            };
            this.renderers.push(TextRenderer(scoreText));

            this.highScoreTextPairs.push({
                username: usernameText,
                score: scoreText,
            });

            curY += 100;
        }

        this.createBackButton(curY - 100, this.onBack);

        this.createRenderersFromButtons();
    };

    public refresh = () => {
        this.createScreen();
    };
}
