import BaseParticleSystem from '../../Engine/Systems/BaseParticleSystem';
import Vec2, { expand } from '../../Engine/Math/Vec2';
import Distribution from '../../Engine/Math/distribution';
import Color from '../../Engine/Graphics/Color';
import { random } from '../../Engine/Math/random';
import Particle from '../../Engine/Graphics/Particle';
import PathedParticle from '../Objects/PathedParticle';
import Path from '../Objects/Path';
import Rectangle from '../../Engine/Geometry/Rectangle';

export interface RandomParams {
    size: Distribution;
    speed: Distribution;
    lifetime: Distribution;
    fromColor: Color;
    // toColor: Color;
}

export default class ParticleSystem extends BaseParticleSystem {
    public update = (elapsedTime: number) => {
        for (let i = this._particles.length - 1; i >= 0; i--) {
            this._particles[i].update(elapsedTime);

            if (!this._particles[i].alive) {
                this._particles.splice(i, 1);
                continue;
            }
        }
    };

    /**
     * Generates new particles in a cone
     * @param center Center to generate particles
     * @param direction Center in degrees
     */
    public carCrash(center: Vec2, direction: number, props: RandomParams) {
        // add flying chunks
        for (let particle = 0; particle < 50; particle++) {
            const size = Math.abs(
                random.nextGaussian(props.size.mean, props.size.stdev),
            );

            const speed = Math.abs(
                random.nextGaussian(props.speed.mean, props.speed.stdev),
            );

            const path = new Path();

            path.addPoint(expand(center));

            const vec2Dir = random.nextAngleInCone(direction);
            path.addPoint({
                x: center.x + vec2Dir.x * 100,
                y: center.y + vec2Dir.y * 100,
            });

            const p = new PathedParticle(path, {
                center: { x: center.x, y: center.y },
                size: { x: size, y: size },
                rotation: 0,
                speed,
                direction: vec2Dir,
                lifetime: random.nextGaussian(
                    props.lifetime.mean,
                    props.lifetime.stdev,
                ),
                fromColor: {
                    r: props.fromColor.r,
                    g: props.fromColor.g,
                    b: props.fromColor.b,
                    a: props.fromColor.a,
                },
            });
            this._particles.push(p);
        }

        // add some stationary chunks

        // add some chunks to the car
    }

    public showerSparks(padBounds: Rectangle, props: RandomParams) {
        const startingY = padBounds.y + 18;
        const startingX = padBounds.x + 11;
        const innerWidth = 54;
        // left & right wall
        [startingX, startingX + innerWidth].map(x => {
            for (let y = startingY; y < padBounds.h; y += 1) {
                const center = { x, y };
                for (let particle = 0; particle < 5; particle++) {
                    const size = Math.abs(
                        random.nextGaussian(props.size.mean, props.size.stdev),
                    );

                    const speed = Math.abs(
                        random.nextGaussian(
                            props.speed.mean,
                            props.speed.stdev,
                        ),
                    );

                    const color = random.coinFlip()
                        ? {
                              r: props.fromColor.r,
                              g: props.fromColor.g,
                              b: props.fromColor.b,
                              a: props.fromColor.a,
                          }
                        : {
                              r: 239,
                              g: 242,
                              b: 191,
                              a: props.fromColor.a,
                          };

                    const p = new Particle({
                        center: center,
                        size: { x: size, y: size },
                        rotation: 0,
                        speed,
                        direction: random.nextAngleInCone(Math.PI),
                        lifetime: random.nextGaussian(
                            props.lifetime.mean,
                            props.lifetime.stdev,
                        ),
                        fromColor: color,
                    });
                    this._particles.push(p);
                }
            }
        });

        // top & bottom
        [startingY].map(y => {
            for (let x = startingX; x < innerWidth + startingX; x += 1) {
                const center = { x, y };
                for (let particle = 0; particle < 5; particle++) {
                    const size = Math.abs(
                        random.nextGaussian(props.size.mean, props.size.stdev),
                    );

                    const speed = Math.abs(
                        random.nextGaussian(
                            props.speed.mean,
                            props.speed.stdev,
                        ),
                    );

                    const color = random.coinFlip()
                        ? {
                              r: props.fromColor.r,
                              g: props.fromColor.g,
                              b: props.fromColor.b,
                              a: props.fromColor.a,
                          }
                        : {
                              r: 239,
                              g: 242,
                              b: 191,
                              a: props.fromColor.a,
                          };

                    const p = new Particle({
                        center: center,
                        size: { x: size, y: size },
                        rotation: 0,
                        speed,
                        direction: random.nextAngleInCone(Math.PI),
                        lifetime: random.nextGaussian(
                            props.lifetime.mean,
                            props.lifetime.stdev,
                        ),
                        fromColor: color,
                    });
                    this._particles.push(p);
                }
            }
        });
    }

    public froggerBlood(deathCenter: Vec2, props: RandomParams) {
        console.log('hello');
        for (let particle = 0; particle < 500; particle++) {
            const size = Math.abs(
                random.nextGaussian(props.size.mean, props.size.stdev),
            );

            const speed = Math.abs(
                random.nextGaussian(props.speed.mean, props.speed.stdev),
            );

            const color = random.coinFlip()
                ? {
                      r: props.fromColor.r,
                      g: props.fromColor.g,
                      b: props.fromColor.b,
                      a: props.fromColor.a,
                  }
                : { r: 51, g: 204, b: 51, a: 1 };

            const p = new Particle({
                center: expand(deathCenter),
                size: { x: size, y: size },
                rotation: 0,
                speed,
                direction: random.nextCircleVector(),
                lifetime: random.nextGaussian(
                    props.lifetime.mean,
                    props.lifetime.stdev,
                ),
                fromColor: color,
            });
            this._particles.push(p);
        }
    }

    public splash(center: Vec2, props: RandomParams) {
        for (let particle = 0; particle < 1000; particle++) {
            const size = Math.abs(
                random.nextGaussian(props.size.mean, props.size.stdev),
            );

            const speed = Math.abs(
                random.nextGaussian(props.speed.mean, props.speed.stdev),
            );

            const p = new Particle({
                center: expand(center),
                size: { x: size, y: size },
                rotation: 0,
                speed,
                direction: random.nextCircleVector(),
                lifetime: random.nextGaussian(
                    props.lifetime.mean,
                    props.lifetime.stdev,
                ),
                fromColor: props.fromColor,
            });
            this._particles.push(p);
        }
    }
}
