import { GameMouseEvent } from '../../Engine/Input/Mouse';
import { CanvasRenderer } from '../../Engine/Renderers/CanvasRenderer';
import ButtonRenderer from '../Renderers/ButtonRenderer';
import Rectangle from '../../Engine/Geometry/Rectangle';
import Button from '../UI/Button';

export default class BaseScreen {
    private internalMouseMove: (gev: GameMouseEvent) => void;
    protected internalMouseClick: (gev: GameMouseEvent) => void;
    protected internalRender: (cr: CanvasRenderer) => void;

    public refresh?: () => void;

    protected buttons: Button[] = [];
    protected readonly buttonW = 400;
    protected readonly buttonX = (1024 - this.buttonW) / 2;

    protected renderers: ((cr: CanvasRenderer) => void)[] = [];

    constructor(public readonly title: string) {
        this.internalMouseClick = this.findMouseClickOnButtons;
        this.internalRender = this.renderButtons;
    }

    protected createRenderersFromButtons = () => {
        for (let i = 0; i < this.buttons.length; i++) {
            this.renderers.push(ButtonRenderer(this.buttons[i]));
        }
    };

    protected clearRenderers = () => {
        console.log('hello!');
        this.renderers = [];
    };

    private findMouseClickOnButtons = (gev: GameMouseEvent) => {
        for (let i = 0; i < this.buttons.length; i++) {
            const oldBounds = this.buttons[i].bounds;
            if (
                new Rectangle(
                    oldBounds.x,
                    oldBounds.y,
                    oldBounds.w,
                    oldBounds.h,
                ).inBounds(gev.pos)
            ) {
                this.buttons[i].onSelect();
                return;
            }
        }
    };

    protected resetMouseClick = () => {
        this.internalMouseClick = this.findMouseClickOnButtons;
    };

    public mouseClick = (gev: GameMouseEvent) => {
        this.internalMouseClick(gev);
    };

    protected renderButtons = (cr: CanvasRenderer) => {
        cr.drawText({ x: cr.middleX, y: 100 }, this.title, 0, true);
        for (let i = 0; i < this.renderers.length; i++) {
            this.renderers[i](cr);
        }
    };

    public render = (cr: CanvasRenderer) => {
        cr.clear();
        this.internalRender(cr);
    };

    /**
     * Gets the x position for a centered thing
     */
    protected getXFromW = (w: number) => {
        return (1024 - w) / 2;
    };

    protected getXAndW = (startingW: number) => {
        return { x: this.getXAndW(startingW), w: startingW };
    };

    protected createBackButton = (curY: number, onSelect: () => void) => {
        const backW = this.buttonW + 50;
        const backX = this.getXFromW(backW);

        this.buttons.push({
            bounds: new Rectangle(backX, (curY += 200), backW, 100),
            text: { content: 'Back To Menu', font: '64px arial' },
            onSelect: onSelect,
        });
    };
}
