import { CanvasRenderer } from '../Engine/Renderers/CanvasRenderer';
import Mouse, { GameMouseEvent } from '../Engine/Input/Mouse';
import { AudioSystem } from '../Engine/Systems/AudioSystem';
import GameLoop from '../Engine/BaseComponents/GameLoop';
import Menu from './Screens/Menu';
import GameModel from './GameModel';
import Keyboard from '../Engine/Input/Keyboard';
import ControlsManager from '../Engine/Managers/ControlsManager';
import GameSettings from '../Engine/Settings/GameSettings';

export default class GamePlay extends GameLoop<CanvasRenderer> {
    private mouse: Mouse;
    private playerKeyboard: Keyboard;
    private internalMouseClick: (gev: GameMouseEvent) => void;
    private internalMouseMove: (gev: GameMouseEvent) => void;

    private menu: Menu;
    private gameModel: GameModel;

    private readonly audioSystem: AudioSystem = new AudioSystem();

    constructor(canvas: HTMLCanvasElement) {
        super(canvas, CanvasRenderer);

        GameSettings.updateWithCanvas(canvas);

        this.menu = new Menu(this.setupNewGame);
        this.gameModel = new GameModel(this.handleGameEscape);

        this.mouse = new Mouse(canvas);
        this.addMouseHandlers();
    }

    public play(): void {
        this.setupMenu();
        this.start();
    }

    private setupMenu = () => {
        this.clearAllInternals();
        this.mouse.clearInputBuffer();

        this.internalProcessInput = () => {
            this.mouse.process();
        };

        this.internalMouseClick = this.menu.mouseClick;
        this.internalRender = this.menu.render;
    };

    private setupNewGame = () => {
        this.clearAllInternals();
        this.mouse.clearInputBuffer();

        this.internalRender = this.gameModel.render;
        this.internalUpdate = this.gameModel.update;
        this.internalMouseClick = this.gameModel.mouseClick;

        this.initializeKeyboard();
        this.internalProcessInput = (et: number) => {
            this.playerKeyboard.process(et);
            this.mouse.process();
        };

        // Should this be here? Attaching methods should be enough?
        // How would the game model know it is a new game?
        this.gameModel.start();
    };

    private initializeKeyboard = () => {
        this.playerKeyboard = new Keyboard(true);

        const controls = ControlsManager.getControls();

        const { register } = this.playerKeyboard;

        register(controls[0], this.gameModel.moveLeft);
        register(controls[1], this.gameModel.moveRight);
        register(controls[2], this.gameModel.moveUp);
        register(controls[3], this.gameModel.moveDown);
        register('Escape', this.handleGameEscape);
    };

    private handleGameEscape = () => {
        this.gameModel.reset();
        this.setupMenu();
    };

    private clearAllInternals = () => {
        this.clearBaseInternals();
        this.internalMouseClick = () => {};
        this.internalMouseMove = () => {};
    };

    private addMouseHandlers = () => {
        this.mouse.register('click', (gev: GameMouseEvent) => {
            this.internalMouseClick(gev);
        });

        this.mouse.register('mousemove', (gev: GameMouseEvent) => {
            this.internalMouseMove(gev);
        });
    };

    public destroy = () => {
        this.mouse.destroy();
        this.audioSystem.destroy();
    };
}
