import IHighScore from '../Interfaces/IHighScore';

export default class HighScoreManager {
    private static storageKey: string = '@frogger-high-scores';

    public static clear = () => {
        localStorage.removeItem(HighScoreManager.storageKey);
    };

    public static getHighScores = () => {
        const highScores = localStorage[HighScoreManager.storageKey];
        if (!highScores) {
            return [];
        } else {
            return JSON.parse(highScores) as IHighScore[];
        }
    };

    /**
     * Sends a username and a score to be added to the high scores.
     *
     * Returns true if it was added, false if it wasn't.
     */
    public static addScore = (username: string, score: number) => {
        let highScores = HighScoreManager.getHighScores();

        highScores.push({ username, score: parseInt(score.toFixed(0)) });
        highScores.sort((a: IHighScore, b: IHighScore) => {
            if (a.score < b.score) {
                return -1;
            } else if (a.score === b.score) {
                return 0;
            } else {
                return 1;
            }
        });

        if (highScores.length > 5) {
            const removedItem = highScores.splice(0, 1)[0];
            if (
                removedItem.score === score &&
                removedItem.username === username
            ) {
                // The object wasn't added, it was removed immediately.
                return false;
            }
        }

        localStorage[HighScoreManager.storageKey] = JSON.stringify(highScores);

        return true;
    };

    public static isHighScore = (score: number) => {
        let highScores = HighScoreManager.getHighScores();

        if (highScores.length < 5) {
            return true;
        }

        for (let i = 0; i < highScores.length; i++) {
            if (highScores[i].score < score) {
                return true;
            }
        }

        return false;
    };
}
