import HomePad from '../Objects/HomePad';
import { CanvasRenderer } from '../../Engine/Renderers/CanvasRenderer';
import SpriteSheet from '../../Engine/Graphics/SpriteSheet';
import settings from '../../../settings';
import { copy } from '../../Engine/Geometry/Rectangle';

const HomePadRenderer = (homePad: HomePad) => (
    cr: CanvasRenderer,
    ss: SpriteSheet,
) => {
    const { x, y, w, h } = homePad.bounds;

    cr.drawSubTexture(ss, 'pad-tile', { x, y }, 0);

    if (homePad.hasFrog) {
        cr.drawSubTexture(ss, 'frogger-idle', { x: x + 13, y }, 0);
    }

    if (homePad.hasFly) {
        cr.drawSubTexture(ss, 'fly', { x: x + 20, y: y + 20 }, 0);
    }

    if (homePad.hasAligator) {
        const newBounds = copy(homePad.bounds);
        newBounds.x += 15;
        cr.drawSubTexture(ss, 'aligator-head', homePad.aligatorPos, 0);
        // cr.drawSubTextureInClip(
        //     ss,
        //     'aligator-head',
        //     homePad.aligatorPos,
        //     true,
        //     newBounds,
        // );
    }

    // debug shapd
    if (settings.showCollisionShapes) {
        cr.drawRectangle(x, y, w, h, 0, 'rgba(0, 170, 255, 0.5');
    }
};

export default HomePadRenderer;
