import PathedObject from '../Objects/PathedObject';
import { CanvasRenderer } from '../../Engine/Renderers/CanvasRenderer';
import SpriteSheet from '../../Engine/Graphics/SpriteSheet';
import AnimatedAnimal from '../Objects/AnimatedAnimal';
import GameSettings from '../../Engine/Settings/GameSettings';

export default class PathedObjectSystem {
    private _objs: PathedObject[] = [];

    private readonly rows: {
        num: number;
        speed: number;
        type: 'log' | 'tur' | 'car';
        asset: string;
    }[] = [
        { num: 0, speed: 0, type: 'car', asset: '' },
        { num: 3, speed: 80 / 1000, type: 'log', asset: 'med-log' },
        { num: 3, speed: 70 / 1000, type: 'tur', asset: 'turtle-1' },
        { num: 2, speed: 80 / 1000, type: 'log', asset: 'big-log' },
        { num: 3, speed: 50 / 1000, type: 'log', asset: 'sml-log' },
        { num: 3, speed: 80 / 1000, type: 'tur', asset: 'turtle-1' },
        { num: 0, speed: 0, type: 'car', asset: '' },
        { num: 1, speed: 100 / 1000, type: 'car', asset: 'semi-truck' },
        { num: 3, speed: 130 / 1000, type: 'car', asset: 'blue-car' },
        { num: 2, speed: 130 / 1000, type: 'car', asset: 'fire-truck' },
        { num: 3, speed: 120 / 1000, type: 'car', asset: 'green-car' },
        { num: 3, speed: 100 / 1000, type: 'car', asset: 'yellow-car' },
        { num: 0, speed: 0, type: 'car', asset: '' },
    ];

    get objects() {
        return this._objs;
    }

    public initializeObjects = (
        numCols: number,
        height: number,
        ss: SpriteSheet,
    ) => {
        this._objs.length = 0;

        for (let row = 0; row < this.rows.length; row++) {
            const newY = height * row;

            const newX = GameSettings.width / this.rows[row].num;

            if (this.rows[row].type === 'tur') {
                const turtleFrames = [1, 2, 3, 4, 5, 6, 7].map(
                    x => `turtle-${x}`,
                );

                const divingFrames = [1, 2, 3, 4, 3, 2, 1].map(
                    x => `turtle-dive-${x}`,
                );
                // create 3 turtles
                for (let i = 0; i < this.rows[row].num; i++) {
                    const curFrames =
                        i === 0
                            ? [...turtleFrames, ...divingFrames]
                            : [...turtleFrames];
                    const { w } = ss.getBounds(this.rows[row].asset);
                    const numAnimals = row === 2 ? 2 : 3;
                    for (let j = 1; j <= numAnimals; j++) {
                        this._objs.push(
                            new AnimatedAnimal(
                                curFrames,
                                i === 0 ? 6000 : 2000,
                                {
                                    speed: this.rows[row].speed,
                                    type: this.rows[row].type,
                                    center: {
                                        x: newX * i + w * j,
                                        y: newY + height / 2,
                                    },
                                    ss,
                                    goingLeft: row % 2 === 0,
                                    asset: this.rows[row].asset,
                                },
                            ),
                        );
                    }
                }
            } else {
                for (let i = 0; i < this.rows[row].num; i++) {
                    if (i === 0 && row === 1) {
                        this._objs.push(
                            new PathedObject(
                                {
                                    speed: this.rows[row].speed,
                                    type: this.rows[row].type,
                                    center: {
                                        x: newX + newX * i,
                                        y: newY + height / 2,
                                    },
                                    ss,
                                    goingLeft: row % 2 === 0,
                                    asset: this.rows[row].asset,
                                },
                                {
                                    frames: ['aligator-idle', 'aligator-chomp'],
                                    loopTime: 4000,
                                },
                            ),
                        );
                        continue;
                    }
                    this._objs.push(
                        new PathedObject({
                            speed: this.rows[row].speed,
                            type: this.rows[row].type,
                            center: {
                                x: newX + newX * i,
                                y: newY + height / 2,
                            },
                            ss,
                            goingLeft: row % 2 === 0,
                            asset: this.rows[row].asset,
                        }),
                    );
                }
            }
        }

        // 3 cars

        // 3 cars

        // 3 cars

        // 1 car

        // 2 cars
    };

    public update = (elapsedTime: number) => {
        for (let i = 0; i < this._objs.length; i++) {
            this._objs[i].update(elapsedTime);
        }
    };

    public render = (cr: CanvasRenderer) => {
        for (let i = 0; i < this._objs.length; i++) {
            this._objs[i].render(cr);
        }
    };
}
