import Rectangle from '../../Engine/Geometry/Rectangle';
import BaseScreen from './BaseScreen';
import TextRenderer from '../../Engine/Renderers/TextRenderer';

export default class Credits extends BaseScreen {
    constructor(onBack: () => void) {
        super('Credits');

        let curY = 200;

        const creditsText = [
            'This work was done by Matt Bishop.',
            'I was him who did it.',
            'Frogger would be fun they said... wait who?',
            'Less is more they say.',
        ];

        for (let i = 0; i < creditsText.length; i++) {
            this.renderers.push(
                TextRenderer({
                    content: `${creditsText[i]}`,
                    pos: { x: 150, y: curY += 50 },
                    font: '32px arial',
                }),
            );
        }

        this.createBackButton(curY, onBack);

        this.createRenderersFromButtons();
    }
}
