/**
 * Base class for a game loop.
 */
export default class GameLoop<RendererType> {
    private previousTimeStamp: number = 0;
    private cr: RendererType;
    protected isGameOver: boolean = false;

    /**
     * Will be called every game loop. You should put mouse or keyboard processes here.
     */
    protected internalProcessInput: (elapsedTime: number) => void;
    /**
     * Will be called every game loop. Should set this to what you need.
     */
    protected internalUpdate: (elapsedTime: number) => void;
    /**
     * Will be called every game loop. Depending on the templated RenderType you will need to use that.
     */
    protected internalRender: (cr: RendererType) => void;

    constructor(
        canvas: HTMLCanvasElement,
        RenderCreator: new (...args: any[]) => RendererType,
    ) {
        this.cr = new RenderCreator(canvas);
    }

    /**
     * Starts the game loop.
     */
    protected start = () => {
        this.previousTimeStamp = performance.now();
        requestAnimationFrame(this.gameLoop);
    };

    /**
     * Clears the internals for process input, update, and render.
     */
    protected clearBaseInternals = () => {
        this.internalProcessInput = () => {};
        this.internalRender = () => {};
        this.internalUpdate = () => {};
    };

    private gameLoop = (timeStamp: number) => {
        const elapsedTime = timeStamp - this.previousTimeStamp;
        this.previousTimeStamp = timeStamp;

        this.processInput(elapsedTime);
        this.update(elapsedTime);
        this.render();

        if (!this.isGameOver) {
            requestAnimationFrame(this.gameLoop);
        }
    };

    private processInput = (elapsedTime: number) => {
        this.internalProcessInput(elapsedTime);
    };

    private update = (elapsedTime: number) => {
        this.internalUpdate(elapsedTime);
    };

    private render = () => {
        this.internalRender(this.cr);
    };
}
