import Dictionary from '../Interfaces/Dictionary';
import GameSettings from '../Settings/GameSettings';

/**
 * Mouse event for the game. Holds the native event, along with the positing of it in the HTML element.
 */
export type GameMouseEvent = { ev: MouseEvent; pos: Vec2 };
type Command = (gameMouseEvent: GameMouseEvent) => void;
type Vec2 = { x: number; y: number };
type SupportedEventTypes = 'click' | 'mousemove';

export default class Mouse {
    private _handlers: Dictionary<Command[]> = {};
    private _inputBuffer: Dictionary<MouseEvent> = {};

    constructor(private htmlElement: HTMLElement) {
        this._addEventListeners();
    }

    public destroy(): void {
        this._removeEventListeners();

        this._handlers = {};
    }

    public resetHandlers(): void {
        this._handlers = {};
    }

    public clearInputBuffer(): void {
        this._inputBuffer = {};
    }

    public register(eventName: SupportedEventTypes, handler: Command) {
        if (!this._handlers[eventName]) {
            this._handlers[eventName] = [];
        }
        this._handlers[eventName].push(handler);
    }

    public process = () => {
        for (const key in this._inputBuffer) {
            if (this._inputBuffer.hasOwnProperty(key)) {
                for (
                    let i = 0;
                    this._handlers[key] && i < this._handlers[key].length;
                    i++
                ) {
                    const ev = this._inputBuffer[key];
                    this._handlers[key][i]({
                        ev,
                        pos: this.getPos(ev),
                    });
                }
            }
        }

        this._inputBuffer = {};
    };

    private _addEventListeners = () => {
        this.htmlElement.addEventListener('click', this._handleInput);
        this.htmlElement.addEventListener('mousemove', this._handleInput);
    };

    private _removeEventListeners = () => {
        this.htmlElement.removeEventListener('click', this._handleInput);
        this.htmlElement.removeEventListener('mousemove', this._handleInput);
    };

    private _handleInput = (ev: MouseEvent) => {
        if (ev.type === 'click' || ev.type === 'mousemove') {
            this._inputBuffer[ev.type] = ev;
        }
    };

    private getPos(event: MouseEvent): Vec2 {
        const rect = this.htmlElement.getBoundingClientRect();
        const x = event.clientX - rect.left;
        const y = event.clientY - rect.top;
        const ratioX = GameSettings.width / rect.width;
        const ratioY = GameSettings.height / rect.height;
        return { x: x * ratioX, y: y * ratioY };
    }
}
