import { CanvasRenderer } from '../Engine/Renderers/CanvasRenderer';
import EventSystem from '../Engine/Systems/EventSystem';
import { AudioSystem } from '../Engine/Systems/AudioSystem';
import HighScoreManager from '../Engine/Managers/HighScoreManager';
import Player from './Objects/Player';
import SpriteSheet from '../Engine/Graphics/SpriteSheet';
import Rectangle from '../Engine/Geometry/Rectangle';
import PathedObjectSystem from './Systems/PathedObjectSystem';
import PathedObject from './Objects/PathedObject';
import HomePadSystem from './Systems/HomePadSystem';
import ParticleSystem from './Systems/ParticleSystem';
import ParticleSystemRenderer from '../Engine/Renderers/ParticleSystemRenderer';
import HomePad from './Objects/HomePad';
import ScoringManager from './Systems/ScoringSystem';
import Button from './UI/Button';
import { GameMouseEvent } from '../Engine/Input/Mouse';
import ButtonRenderer from './Renderers/ButtonRenderer';
import GameSettings from '../Engine/Settings/GameSettings';
import Keyboard from '../Engine/Input/Keyboard';
import { genCharArray } from './Helpers/character';

type TimeFunc = (elapsedTime: number) => void;
type RenderFunc = (cr: CanvasRenderer) => void;

export default class GameModel {
    private scoreManager: ScoringManager = new ScoringManager();
    /**
     * The player has 30 seconds to guide the frog to its home.
     *
     * This resets on every death or reset.
     */
    private time: number = 30000;

    private lives: number = 7;

    private readonly rowImages: string[] = [
        'bush-tile',
        'water-tile',
        'water-tile',
        'water-tile',
        'water-tile',
        'water-tile',
        'grass-tile',
        'street-tile',
        'street-tile',
        'street-tile',
        'street-tile',
        'street-tile',
        'grass-tile',
    ];

    private readonly requiredOnHomePads = 5;
    private currentOnHomePad = 0;
    private countDownTime: boolean = false;

    private readonly numRows: number = this.rowImages.length;
    private readonly tileHeight = GameSettings.width / this.numRows;
    private readonly tileWidth = GameSettings.width / this.numRows;

    private internalUpdate: TimeFunc;
    private internalRender: RenderFunc;
    private internalMoveLeft: () => void;
    private internalMoveRight: () => void;
    private internalMoveUp: () => void;
    private internalMoveDown: () => void;
    private internalPlayerRender: (cr: CanvasRenderer) => void;
    private internalDetectCollisions: () => void;
    private internalMouseClick: (ev: GameMouseEvent) => void = () => {};

    private readonly eventSystem: EventSystem = new EventSystem();
    private audioSystem: AudioSystem = new AudioSystem();
    private pathedObjectSystem: PathedObjectSystem = new PathedObjectSystem();
    private homePadSystem: HomePadSystem;
    private particleSystem: ParticleSystem = new ParticleSystem();
    private particleSystemRenderer: (cr: CanvasRenderer) => void;

    private crashParticleParams = {
        size: { mean: 5, stdev: 2 },
        speed: { mean: 20 / 1000, stdev: 9 / 1000 },
        lifetime: { mean: 2000, stdev: 500 },
        fromColor: { r: 51, g: 204, b: 51, a: 1 },
    };

    private sparkParams = {
        size: { mean: 2, stdev: 0 },
        speed: { mean: 4 / 1000, stdev: 1 / 1000 },
        lifetime: { mean: 2000, stdev: 200 },
        fromColor: { r: 235, g: 247, b: 4, a: 1 },
    };

    private aligatorDeathParams = {
        size: { mean: 2, stdev: 1 },
        speed: { mean: 25 / 1000, stdev: 25 / 1000 },
        lifetime: { mean: 250, stdev: 200 },
        fromColor: { r: 234, g: 25, b: 25, a: 1 },
    };

    private splashParams = {
        size: { mean: 1, stdev: 1 },
        speed: { mean: 20 / 1000, stdev: 20 / 1000 },
        lifetime: { mean: 250, stdev: 200 },
        fromColor: { r: 0, g: 153, b: 115, a: 1 },
    };

    private explosionParams = {
        size: { mean: 1, stdev: 1 },
        speed: { mean: 20 / 1000, stdev: 20 / 1000 },
        lifetime: { mean: 250, stdev: 200 },
        fromColor: { r: 51, g: 204, b: 51, a: 1 },
    };

    private _spriteSheet = new SpriteSheet(
        'graphics/frogger-game-sprites.png',
        {
            // Frogger
            'frogger-idle': new Rectangle(0, 2, 51, 73),
            'frogger-1': new Rectangle(54, 2, 57, 73),
            'frogger-2': new Rectangle(114, 2, 56, 73),
            'frogger-3': new Rectangle(173, 2, 53, 73),
            'frogger-4': new Rectangle(230, 2, 54, 73),
            // Tiles
            'grass-tile': new Rectangle(135, 158, 82, 82),
            'water-tile': new Rectangle(226, 158, 82, 82),
            'street-tile': new Rectangle(316, 158, 82, 82),
            'bush-tile': new Rectangle(407, 158, 82, 82),
            'pad-tile': new Rectangle(497, 158, 82, 82),
            // Cars
            'yellow-car': new Rectangle(305, 482, 134, 71),
            'green-car': new Rectangle(155, 483, 135, 71),
            'blue-car': new Rectangle(12, 483, 127, 69),
            'fire-truck': new Rectangle(9, 407, 178, 66),
            'semi-truck': new Rectangle(203, 406, 285, 66),
            // Logs
            'big-log': new Rectangle(13, 258, 353, 59),
            'med-log': new Rectangle(14, 328, 273, 59),
            'sml-log': new Rectangle(387, 258, 185, 59),
            // Other
            fly: new Rectangle(84, 171, 43, 48),
            // Turtle
            'turtle-1': new Rectangle(403, 11, 71, 57),
            'turtle-2': new Rectangle(480, 9, 70, 61),
            'turtle-3': new Rectangle(7, 83, 71, 67),
            'turtle-4': new Rectangle(80, 82, 71, 68),
            'turtle-5': new Rectangle(154, 84, 70, 66),
            'turtle-6': new Rectangle(229, 89, 71, 56),
            'turtle-7': new Rectangle(301, 88, 71, 57),
            // Turtle Diving
            'turtle-dive-1': new Rectangle(377, 92, 60, 50),
            'turtle-dive-2': new Rectangle(453, 98, 49, 39),
            'turtle-dive-3': new Rectangle(517, 102, 36, 28),
            'turtle-dive-4': new Rectangle(570, 104, 20, 20),
            // Aligator
            'aligator-idle': new Rectangle(299, 317, 180, 61),
            'aligator-chomp': new Rectangle(520, 345, 61, 180),
            'aligator-head': new Rectangle(455, 499, 37, 43),
        },
    );
    private player: Player;

    constructor(private readonly onGameOver: () => void) {
        this.particleSystemRenderer = ParticleSystemRenderer(
            this.particleSystem,
        );
    }

    public readonly start = () => {
        console.log('Starting Game');
        this.audioSystem.clearAllSounds();
        this.audioSystem.addAudio(
            'audio/sound-frogger-background.mp3',
            0,
            true,
            0.5,
        );

        this.eventSystem.clear();

        this.homePadSystem = new HomePadSystem(
            this._spriteSheet,
            this.tileWidth,
            this.tileHeight,
        );

        this.initializePlayer();

        this.pathedObjectSystem.initializeObjects(
            this.numRows,
            this.tileHeight,
            this._spriteSheet,
        );

        this.particleSystem.clear();

        this.internalUpdate = this.gameUpdate;
        this.internalRender = this.gameRender;
        this.internalDetectCollisions = this.gameDetectCollisions;

        this.time = 30000;
        this.lives = 7;
        this.countDownTime = true;
        this.scoreManager.resetScore();
    };

    public readonly update = (elapsedTime: number) => {
        this.internalUpdate(elapsedTime);
    };

    public readonly render = (cr: CanvasRenderer) => {
        this.internalRender(cr);
    };

    public readonly destroy = () => {
        this.eventSystem.destroy();
        this.audioSystem.destroy();
    };

    public readonly reset = () => {
        this.audioSystem.clearAllSounds();
        this.particleSystem.clear();

        this.clearPlayerInternals();

        this.currentOnHomePad = 0;
    };

    public readonly moveLeft = () => {
        this.internalMoveLeft();
    };

    public readonly moveRight = () => {
        this.internalMoveRight();
    };

    public readonly moveUp = () => {
        this.internalMoveUp();
    };

    public readonly moveDown = () => {
        this.internalMoveDown();
    };

    private readonly initializePlayer = () => {
        this.player = new Player(
            {
                center: {
                    x: GameSettings.width / 2,
                    y: GameSettings.width - this.tileHeight / 2,
                },
                collisionRadius: 20,
                width: this.tileWidth,
                spriteSheet: this._spriteSheet,
                delta: this.tileWidth,
            },
            this.audioSystem,
        );

        this.setPlayerInternals();
    };

    private readonly setPlayerInternals = () => {
        this.internalMoveLeft = this.player.moveLeft;
        this.internalMoveRight = this.player.moveRight;
        this.internalMoveUp = () => {
            this.scoreManager.moveUp();
            this.player.moveUp();
        };
        this.internalMoveDown = () => {
            this.scoreManager.moveDown();
            this.player.moveDown();
        };
        this.internalPlayerRender = this.player.render;
    };

    private readonly clearPlayerInternals = () => {
        this.internalMoveLeft = () => {};
        this.internalMoveRight = () => {};
        this.internalMoveUp = () => {};
        this.internalMoveDown = () => {};
        this.internalPlayerRender = () => {};
    };

    private readonly gameRender = (cr: CanvasRenderer) => {
        cr.clear();

        this.drawBackground(cr);
        this.homePadSystem.render(cr);

        this.pathedObjectSystem.render(cr);

        this.drawGameStatus(cr);

        this.internalPlayerRender(cr);

        this.particleSystemRenderer(cr);
    };

    private readonly drawBackground = (cr: CanvasRenderer) => {
        cr.drawRectangle(0, 0, 1024, 1024, 0, '#000');

        for (let row = 0; row < this.numRows; row++) {
            const newY = this.tileHeight * row;
            for (let col = 0; col < this.numRows; col++) {
                const newX = this.tileWidth * col;
                cr.drawSubTexture(
                    this._spriteSheet,
                    this.rowImages[row],
                    {
                        x: newX + this.tileWidth / 2,
                        y: newY + this.tileWidth / 2,
                    },
                    0,
                    true,
                );
            }
        }
    };

    private drawGameStatus(cr: CanvasRenderer) {
        cr.drawRectangle(
            0,
            GameSettings.width,
            GameSettings.width,
            GameSettings.width + 100,
            0,
            '#000',
        );

        this.drawTime(cr);
        this.drawScore(cr);
        this.drawLifes(cr);
    }

    private drawTime(cr: CanvasRenderer) {
        const width = 800;
        const padding = 10;
        const height = 50 - padding;

        cr.drawText(
            { x: padding / 2, y: GameSettings.width + 50 + padding },
            'TIME',
            0,
            false,
            '#fff',
            '32px arial',
        );

        const x = 100;
        const y = GameSettings.width + 50 + padding / 2;
        for (let i = 0; i < this.time / 1000; i++) {
            cr.drawRectangle(
                x + i * (width / 30),
                y,
                width / 30 + 1,
                height,
                0,
                '#00ffcc',
            );
        }
    }

    private drawScore(cr: CanvasRenderer) {
        cr.drawText(
            { x: GameSettings.width / 2, y: GameSettings.width + 5 },
            `Score: ${this.scoreManager.score}`,
            0,
            true,
            '#fff',
            '32px arial',
        );
    }

    private drawLifes(cr: CanvasRenderer) {
        for (let i = 0; i < this.lives - 1; i++) {
            cr.drawSubTexture(
                this._spriteSheet,
                'frogger-idle',
                { x: i * 50, y: GameSettings.width - 20 },
                0,
            );
        }
    }

    private readonly gameUpdate = (elapsedTime: number) => {
        this.audioSystem.update(elapsedTime);

        this.eventSystem.update(elapsedTime);

        this.particleSystem.update(elapsedTime);

        this.pathedObjectSystem.update(elapsedTime);

        this.homePadSystem.update(elapsedTime);

        this.player.update(elapsedTime);

        this.updateTime(elapsedTime);

        this.detectCollisions();
    };

    private updateTime(elapsedTime: number) {
        if (!this.countDownTime) {
            return;
        }
        this.time -= elapsedTime;
        if (this.time <= 0) {
            this.playerExplosionAndMoveOn();
            this.countDownTime = false;
        }
    }

    private playerExplosionAndMoveOn = () => {
        this.internalDetectCollisions = () => {};

        this.particleSystem.splash(this.player.center, this.explosionParams);

        this.audioSystem.clearAllSounds();
        this.audioSystem.addAudio(
            'audio/sound-frogger-plunk.mp3',
            0,
            false,
            0.5,
        );

        this.internalPlayerRender = () => {};
        this.startTimedTransition(this.loseALifeAndReset);
    };

    private readonly detectCollisions = () => {
        this.internalDetectCollisions();
    };

    private readonly gameDetectCollisions = () => {
        // It looked like collision happened when the player was idle
        // 1:28 in the video
        if (!this.player.idle) {
            return;
        }

        if (this.player.center.y < this.tileHeight * 2) {
            const { pads } = this.homePadSystem;
            for (let i = 0; i < pads.length; i++) {
                if (pads[i].inBounds(this.player.center)) {
                    this.handleLandedOnHomePad(pads[i]);

                    return;
                }
            }
        }

        const objs = this.pathedObjectSystem.objects;
        for (let i = 0; i < objs.length; i++) {
            if (objs[i].testPoint(this.player.center)) {
                switch (objs[i].type) {
                    case 'car':
                        this.handleCollisionWithCar(objs[i].goingLeft);
                        break;
                    case 'log':
                        this.handleOnLog(objs[i]);
                        break;
                    case 'tur':
                        this.handleOnTurtle(objs[i]);
                        break;
                    case 'alligator':
                        this.handleAlligator(objs[i]);
                        break;
                    default:
                        console.error('NOT IMPLEMENT YET FOR', objs[i].type);
                        break;
                }
                return;
            }
        }

        if (this.player.center.y < GameSettings.width / 2) {
            // console.log('Water');
            this.handleDrown();
        }
        //  else {
        //     console.log('Land');
        // }
    };

    private readonly handleLandedOnHomePad = (homePad: HomePad) => {
        this.internalDetectCollisions = () => {};

        console.log('LANDED ON HOME PAD');

        // handle if there is an aligator
        if (homePad.hasAligator) {
            homePad.hasAligator = false;
            this.handleAlligator();
            return;
        }

        // handle if there is already a frog there.
        if (homePad.hasFrog) {
            this.handleDrown();
            return;
        }

        this.internalPlayerRender = () => {};
        homePad.hasFrog = true;

        this.particleSystem.showerSparks(homePad.bounds, this.sparkParams);
        this.audioSystem.clearAllSounds();
        this.audioSystem.addAudio(
            'audio/sound-frogger-coin-in.mp3',
            0,
            false,
            0.5,
        );

        // update score
        this.scoreManager.landOnPad();

        // handle if there is a fly
        if (homePad.hasFly) {
            homePad.hasFly = false;
            this.scoreManager.gotFly();
        }

        this.currentOnHomePad++;
        if (this.currentOnHomePad >= this.requiredOnHomePads) {
            this.handleBeatLevel();
        } else {
            this.startTimedTransition(this.resetWithNewFrog);
        }
    };

    private handleBeatLevel = () => {
        this.countDownTime = false;

        // player gets 1000 points
        this.scoreManager.beatLevel();
        this.scoreManager.accountForTime(this.time);

        // show score screen,
        this.beatGameScreen();
    };

    private resetWithNewFrog = () => {
        this.scoreManager.resetForNewFrog();
        this.resetPlayer();

        this.audioSystem.clearAllSounds();
        this.audioSystem.addAudio(
            'audio/sound-frogger-background.mp3',
            0,
            true,
            0.5,
        );

        this.scoreManager.accountForTime(this.time);
        this.countDownTime = true;
        this.time = 30000;
    };

    private readonly handleOnLog = (pathedObject: PathedObject) => {
        this.player.speedX =
            pathedObject.speed * (pathedObject.goingLeft ? -1 : 1);
    };

    private readonly handleOnTurtle = (pathedObject: PathedObject) => {
        // if the turtle is not diving, keep up with it
        if (pathedObject.canStandOn) {
            this.player.speedX =
                pathedObject.speed * (pathedObject.goingLeft ? -1 : 1);
        } else {
            this.handleDrown();
        }
    };

    private readonly handleCollisionWithCar = (goingLeft: boolean) => {
        this.internalDetectCollisions = () => {};
        console.log('Crashed!');

        this.audioSystem.clearAllSounds();
        this.audioSystem.addAudio(
            'audio/sound-frogger-squash.mp3',
            0,
            false,
            0.5,
        );
        this.particleSystem.carCrash(
            this.player.center,
            goingLeft ? Math.PI / 2 : -Math.PI / 2,
            this.crashParticleParams,
        );
        this.internalPlayerRender = () => {};
        this.startTimedTransition(this.loseALifeAndReset);
    };

    private readonly handleDrown = () => {
        this.internalDetectCollisions = () => {};
        console.log('Drown');

        this.particleSystem.splash(this.player.center, this.splashParams);

        this.audioSystem.clearAllSounds();
        this.audioSystem.addAudio(
            'audio/sound-frogger-plunk.mp3',
            0,
            false,
            0.5,
        );

        this.internalPlayerRender = () => {};
        this.startTimedTransition(this.loseALifeAndReset);
    };

    private readonly handleAlligator = (pathedObject?: PathedObject) => {
        if (pathedObject && pathedObject.canStandOn) {
            this.player.speedX =
                pathedObject.speed * (pathedObject.goingLeft ? -1 : 1);
            return;
        }
        this.internalDetectCollisions = () => {};

        this.audioSystem.clearAllSounds();
        this.audioSystem.addAudio(
            'audio/sound-frogger-plunk.mp3',
            0,
            false,
            0.5,
        );

        this.internalPlayerRender = () => {};
        this.particleSystem.froggerBlood(
            this.player.center,
            this.aligatorDeathParams,
        );
        this.startTimedTransition(this.loseALifeAndReset);
    };

    private startTimedTransition = (
        callback: () => void,
        duration: number = 2000,
    ) => {
        this.eventSystem.addEvent(duration, 1, callback);
    };

    private loseALifeAndReset = () => {
        this.lives--;

        if (this.lives <= 0) {
            this.lostGameScreen();
            return;
        }

        this.time = 30000;
        this.countDownTime = true;
        this.resetPlayer();
        this.audioSystem.addAudio(
            'audio/sound-frogger-background.mp3',
            0,
            true,
            0.5,
        );
    };

    public readonly mouseClick = (ev: GameMouseEvent) => {
        this.internalMouseClick(ev);
    };

    private readonly endGame = (userName: string, topFiveScore: boolean) => {
        // Add score to high score manager
        if (topFiveScore) {
            HighScoreManager.addScore(
                userName === '' ? 'Default_User' : userName,
                this.scoreManager.score,
            );
        }

        this.reset();
        this.onGameOver();
    };

    private readonly beatGameScreen = () => {
        this.internalDetectCollisions = () => {};
        this.clearPlayerInternals();

        const topFiveScore = HighScoreManager.isHighScore(
            this.scoreManager.score,
        );

        let userName = '';

        if (topFiveScore) {
            const typingKeyboard: Keyboard = new Keyboard(true);
            const registerLetter = (letter: string) => {
                typingKeyboard.register(letter, () => {
                    if (userName.length < 20) {
                        userName += letter;
                    }
                });
            };

            genCharArray('a', 'z').forEach(registerLetter);
            genCharArray('A', 'Z').forEach(registerLetter);

            typingKeyboard.register('Backspace', () => {
                if (userName.length > 0) {
                    userName = userName.slice(0, userName.length - 1);
                } else {
                    userName = '';
                }
            });

            this.internalUpdate = (elapsedTime: number) => {
                typingKeyboard.process(elapsedTime);
                this.gameUpdate(elapsedTime);
            };
        }

        const buttonW = 350;
        const buttonH = 100;
        const bounds = new Rectangle(
            (GameSettings.width - buttonW) / 2,
            (GameSettings.width - buttonH) / 2 + 100,
            buttonW,
            buttonH,
        );

        this.internalMouseClick = (ev: GameMouseEvent) => {
            if (bounds.inBounds(ev.pos)) {
                this.endGame(userName, topFiveScore);
            }
        };

        const bgWidth = 550;
        const bgHeight = 500;

        this.internalRender = (cr: CanvasRenderer) => {
            this.gameRender(cr);
            cr.drawRectangle(
                (GameSettings.width - bgWidth) / 2,
                (GameSettings.width - bgHeight) / 2,
                bgWidth,
                bgHeight,
                0,
                '#fff',
            );
            cr.drawText(
                { x: 512, y: (GameSettings.width - bgHeight) / 2 + 40 },
                'Game Over, You Won! :)',
                0,
                true,
                '#ffaa00',
                '48px arial',
            );

            cr.drawText(
                { x: 512, y: (GameSettings.width - bgHeight) / 2 + 100 },
                `Your Score: ${this.scoreManager.score}`,
                0,
                true,
                '#000',
                '40px arial',
            );

            if (topFiveScore) {
                cr.drawText(
                    { x: 512, y: (GameSettings.width - bgHeight) / 2 + 160 },
                    `Enter Username:`,
                    0,
                    true,
                    '#000',
                    '40px arial',
                );

                cr.drawText(
                    { x: 512, y: (GameSettings.width - bgHeight) / 2 + 200 },
                    `${userName}`,
                    0,
                    true,
                    '#000',
                    '40px arial',
                );
            } else {
                cr.drawText(
                    { x: 512, y: (GameSettings.width - bgHeight) / 2 + 160 },
                    `You didn't get a top 5 score.`,
                    0,
                    true,
                    '#000',
                    '40px arial',
                );
                cr.drawText(
                    { x: 512, y: (GameSettings.width - bgHeight) / 2 + 200 },
                    `Better luck next time!`,
                    0,
                    true,
                    '#000',
                    '40px arial',
                );
            }

            cr.drawRectangle(bounds.x, bounds.y, bounds.w, bounds.h, 0, '#000');

            cr.drawText(
                { x: 512, y: bounds.y + 30 },
                'Back To Menu',
                0,
                true,
                '#ffaa00',
                '48px arial',
            );
        };
    };

    private readonly lostGameScreen = () => {
        this.countDownTime = false;
        this.internalDetectCollisions = () => {};
        this.clearPlayerInternals();

        const buttonW = 350;
        const buttonH = 100;
        const bounds = new Rectangle(
            (GameSettings.width - buttonW) / 2,
            (GameSettings.width - buttonH) / 2,
            buttonW,
            buttonH,
        );

        this.internalMouseClick = (ev: GameMouseEvent) => {
            if (bounds.inBounds(ev.pos)) {
                this.reset();
                this.onGameOver();
            }
        };

        const bgWidth = 550;
        const bgHeight = 500;

        this.internalRender = (cr: CanvasRenderer) => {
            this.gameRender(cr);
            cr.drawRectangle(
                (GameSettings.width - bgWidth) / 2,
                (GameSettings.width - bgHeight) / 2,
                bgWidth,
                bgHeight,
                0,
                '#fff',
            );
            cr.drawText(
                { x: 512, y: (GameSettings.width - bgHeight) / 2 + 40 },
                'Game Over, You Lost :(',
                0,
                true,
                '#ffaa00',
                '48px arial',
            );

            cr.drawRectangle(bounds.x, bounds.y, bounds.w, bounds.h, 0, '#000');

            cr.drawText(
                { x: 512, y: bounds.y + 30 },
                'Back To Menu',
                0,
                true,
                '#ffaa00',
                '48px arial',
            );
        };
    };

    private resetPlayer = () => {
        this.initializePlayer();
        this.internalDetectCollisions = this.gameDetectCollisions;
    };
}
