export default class GameSettings {
    private static _width: number = 1024;
    private static _height: number = 1024;

    public static updateWithCanvas(canvas: HTMLCanvasElement) {
        GameSettings._width = canvas.width;
        GameSettings._height = canvas.height;
    }

    static get width() {
        return this._width;
    }

    static get height() {
        return this._height;
    }
}
