import BaseParticleSystem from '../Systems/BaseParticleSystem';
import { CanvasRenderer } from './CanvasRenderer';

const ParticleSystemRenderer = (particleSystem: BaseParticleSystem) => (
    cr: CanvasRenderer,
) => {
    const { particles } = particleSystem;

    for (let i = particles.length - 1; i >= 0; i--) {
        const { center, rotation, size, fill } = particles[i];
        cr.drawRectangle(
            center.x,
            center.y,
            size.x,
            size.y,
            rotation,
            fill,
            '#ff3300',
        );
    }
};

export default ParticleSystemRenderer;
