import Vec2, { distSqrd, dist, diff } from '../../Engine/Math/Vec2';

/**
 * Represents a path of points
 */
export default class Path {
    private points: Vec2[] = [];
    private curPointIndex: number = 0;
    private curDistanceTraveled: number = 0;

    // getting percent done? For knowing when to fire stuff?
    // could just use the elapsed time

    /**
     * Creates a new path
     * @param onEndPath Gets called when the path is finished
     */
    constructor(
        private loop: boolean = false,
        private onEndPath?: () => void,
        private wrap: boolean = false,
    ) {}

    public addPoint = (point: Vec2) => {
        this.points.push(point);
    };

    /**
     * Travel a certain distance on the path
     * @param distance Distance to travel
     * @returns The current point on a path
     */
    public travel(distance: number): Vec2 {
        let nextIndex = this.curPointIndex + 1;

        if (this.curPointIndex >= this.points.length - 1) {
            if (this.loop) {
                this.curPointIndex = this.points.length - 1;
                nextIndex = 0;
            } else if (this.wrap) {
                this.curPointIndex = 0;
                this.curDistanceTraveled = 0;
                nextIndex = 1;
            } else {
                // console.log('Ending');
                // call onEnded?
                if (this.onEndPath) {
                    this.onEndPath();
                }
                return this.points[this.points.length - 1];
            }
        }

        let distBetweenPoints = dist(
            this.points[this.curPointIndex],
            this.points[nextIndex],
        );

        this.curDistanceTraveled += distance;

        // skip over points until we land between two of them
        // or we reach the end
        while (this.curDistanceTraveled >= distBetweenPoints) {
            this.curDistanceTraveled -= distBetweenPoints;

            if (this.loop) {
                this.curPointIndex =
                    (this.curPointIndex + 1) % this.points.length;

                nextIndex =
                    this.curPointIndex === 0 ? 1 : this.curPointIndex + 1;
            } else {
                this.curPointIndex++;
                nextIndex = this.curPointIndex + 1;
            }

            if (this.curPointIndex >= this.points.length - 1) {
                if (this.loop) {
                    nextIndex = 0;
                } else if (this.wrap) {
                    this.curPointIndex = 0;
                    this.curDistanceTraveled = 0;
                    nextIndex = 1;
                } else {
                    // console.log('Ending 2');
                    // call onEnded?
                    if (this.onEndPath) {
                        this.onEndPath();
                    }
                    return this.points[this.points.length - 1];
                }
            }
        }

        const a = this.points[this.curPointIndex];
        const b = this.points[nextIndex];

        distBetweenPoints = dist(a, b);

        const diffBetweenPoints = diff(a, b);

        const traveledRatio = this.curDistanceTraveled / distBetweenPoints;

        return {
            x: a.x + diffBetweenPoints.x * traveledRatio,
            y: a.y + diffBetweenPoints.y * traveledRatio,
        };
    }

    get lengthSquared() {
        let length = 0;

        for (let i = 0; i < this.points.length; i++) {
            const nextIndex = i + 1;
            if (nextIndex >= this.points.length) {
                return length;
            }
            length += distSqrd(this.points[i], this.points[nextIndex]);
        }

        return length;
    }

    get length() {
        return Math.sqrt(this.lengthSquared);
    }

    get numberOfPoints() {
        return this.points.length;
    }

    public clear = () => {
        this.points.length = 0;
        this.curDistanceTraveled = 0;
        this.curPointIndex = 0;
    };
}
