import PathedObject, { Props } from './PathedObject';
import Animation from '../../Engine/Animation/Animation';
import { CanvasRenderer } from '../../Engine/Renderers/CanvasRenderer';
import settings from '../../../settings';

export default class AnimatedAnimal extends PathedObject {
    private _anim: Animation;

    constructor(frames: string[], duration: number, props: Props) {
        super(props);

        this._anim = new Animation(frames, duration, true);
    }

    public update = (elapsedTime: number) => {
        const newPoint = this.state.path.travel(this.state.speed * elapsedTime);

        this.state.center = newPoint;
        this.state.bounds.x = newPoint.x - this.state.bounds.w / 2;
        this.state.bounds.y = newPoint.y - this.state.bounds.h / 2;

        this._anim.watch(elapsedTime);
    };

    public render = (cr: CanvasRenderer) => {
        cr.drawSubTexture(
            this.props.ss,
            this._anim.currentFrame,
            {
                x: this.state.center.x,
                y: this.state.center.y,
            },
            this.props.goingLeft ? Math.PI : 0,
            true,
        );

        // // center
        if (settings.showCollisionShapes) {
            cr.drawCircle(
                this.state.center.x,
                this.state.center.y,
                10,
                '#fff',
                '#f00',
            );
            // collision box
            cr.drawRectangle(
                this.state.bounds.x,
                this.state.bounds.y,
                this.state.bounds.w,
                this.state.bounds.h,
                0,
                'rgba(00, 104, 255, 0.5)',
            );
        }
    };

    get canStandOn() {
        // add case for turtle and aligator
        return !/\w+-dive-4/.test(this._anim.currentFrame);
    }
}
