import { IUpdateable } from '../Interfaces/IUpdateable';
import { GameAudio } from '../Audio/Audio';

export class AudioSystem implements IUpdateable {
    private audio: GameAudio[] = [];
    public addAudio = (
        source: string,
        delay: number = 0,
        loop: boolean = false,
        volume: number = 1,
        callback?: () => void,
    ) => {
        this.audio.push(new GameAudio(source, delay, loop, volume, callback));
    };
    public update = (elapsedTime: number) => {
        for (let i = this.audio.length - 1; i >= 0; i--) {
            this.audio[i].update(elapsedTime);

            if (this.audio[i].done) {
                this.audio.splice(i, 1);
                continue;
            }
            this.audio[i].update(elapsedTime);
        }
    };

    public clearAllSounds = () => {
        for (let i = 0; i < this.audio.length; i++) {
            this.audio[i].pause();
        }
        this.audio.length = 0;
    };

    public destroy = () => {
        for (let i = 0; i < this.audio.length; i++) {
            this.audio[i].destory();
        }
        this.audio.length = 0;
    };
}
