import Button from '../UI/Button';
import { CanvasRenderer } from '../../Engine/Renderers/CanvasRenderer';

const ButtonRenderer = (button: Button) => (cr: CanvasRenderer) => {
    const { x, y, w, h } = button.bounds;

    cr.drawRectangle(x, y, w, h, 0, button.style?.fill ?? '#000');

    const { content, font } = button.text;

    // Center text withing the rectangle.
    const textWidth = cr.getTextWidth(content, font);
    const textHeight = cr.getFontHeight(font);

    const textXOff = (textWidth - w) / 2;
    const textYOff = (textHeight - h) / 2;

    cr.drawText(
        { x: x - textXOff, y: y - textYOff },
        content,
        0,
        false,
        button.text.fill ?? '#fff',
        font,
    );
};

export default ButtonRenderer;
