import { CanvasRenderer } from '../../Engine/Renderers/CanvasRenderer';
import SpriteSheet from '../../Engine/Graphics/SpriteSheet';
import HomePad from '../Objects/HomePad';
import HomePadRenderer from '../Renderers/HomePadRenderer';
import { random } from '../../Engine/Math/random';
import GameSettings from '../../Engine/Settings/GameSettings';

export default class HomePadSystem {
    private homePads: HomePad[] = [];
    private renderers: ((cr: CanvasRenderer, ss: SpriteSheet) => void)[] = [];

    private readonly timeSpacing: number = 13000;
    /**
     * Time in between special events like putting a fly or aligator.
     */
    private timeSinceLastSpecial: number = 0;

    constructor(
        private ss: SpriteSheet,
        private tileWidth: number,
        private tileHeight: number,
    ) {
        this.homePads.length = 0;
        this.renderers.length = 0;

        const numPads = 5;
        const padding = 100;
        const padSpacing = (GameSettings.width - padding * 2) / (numPads - 1);

        for (let i = 0; i < numPads; i++) {
            const x = padding + i * padSpacing;
            const pad = new HomePad(
                x,
                this.tileHeight / 2,
                this.tileWidth,
                this.tileHeight,
            );

            this.homePads.push(pad);
            this.renderers.push(HomePadRenderer(pad));
        }
    }

    public readonly update = (elapsedTime: number) => {
        this.timeSinceLastSpecial += elapsedTime;

        if (this.timeSinceLastSpecial >= this.timeSpacing) {
            this.timeSinceLastSpecial -= this.timeSpacing;

            const available = this.homePads.filter(x => !x.hasFrog);

            if (available.length === 0) {
                return;
            }

            const chosenIndex = random.nextRange(0, available.length);

            if (random.coinFlip()) {
                // fly
                available[chosenIndex].putFly();
            } else {
                // aligator
                available[chosenIndex].putAligator();
            }
            // this.homePads[available.length - 1].putAligator();
        }

        for (let i = 0; i < this.homePads.length; i++) {
            this.homePads[i].update(elapsedTime);
        }
    };

    public readonly render = (cr: CanvasRenderer) => {
        for (let i = 0; i < this.renderers.length; i++) {
            this.renderers[i](cr, this.ss);
        }
    };

    get pads() {
        return this.homePads;
    }
}
