import Vec2 from '../Math/Vec2';
import Color, { createColorTransition, Colors } from './Color';

export interface Props {
    /**
     * Lifespan in ms of the particle
     */
    readonly lifetime: number;
    center: Vec2;
    /**
     * Initial angle for the particle to spin
     */
    rotation: number;
    readonly speed: number;
    readonly direction: Vec2;
    /**
     * Width and height of the particle
     */
    readonly size: Vec2;
    /**
     * Initial particle color
     */
    readonly fromColor: Color;
    // /**
    //  * Color to transition to throught the particles lifetime
    //  */
    // readonly toColor: Color;
    // readonly fadeTransition: number;
}

export default class Particle {
    /**
     * Elapsed time since particle creation
     */
    protected elapsedTime: number = 0;
    private color: Color;
    private colorTransition: Color;
    private fading: boolean = false;

    constructor(protected props: Props) {
        this.color = props.fromColor;
        // this.colorTransition = createColorTransition(
        //     props.fromColor,
        //     props.toColor,
        //     props.lifetime,
        // );
    }
    get alive() {
        return this.elapsedTime <= this.props.lifetime;
    }

    get center() {
        return this.props.center;
    }

    get rotation() {
        return this.props.rotation;
    }

    get fill() {
        return `rgb(${this.color.r},${this.color.g},${this.color.b})`;
    }

    get size() {
        return this.props.size;
    }

    public update = (elapsedTime: number) => {
        const { speed, direction } = this.props;
        this.center.x += speed * direction.x * elapsedTime;
        this.center.y += speed * direction.y * elapsedTime;
        this.elapsedTime += elapsedTime;

        // this.updateColor(elapsedTime);

        // this.props.rotation += speed * 0.5;
    };

    // private updateColor = (elapsedTime: number) => {
    //     if (
    //         !this.fading &&
    //         this.elapsedTime >= this.props.lifetime - this.props.fadeTransition
    //     ) {
    //         this.fading = true;
    //         this.colorTransition = createColorTransition(
    //             this.color,
    //             { r: this.color.r, g: this.color.g, b: this.color.b, a: 0 },
    //             this.props.fadeTransition,
    //         );
    //     }

    //     this.color.r -= this.colorTransition.r * elapsedTime;
    //     this.color.g -= this.colorTransition.g * elapsedTime;
    //     this.color.b -= this.colorTransition.b * elapsedTime;
    //     this.color.a -= this.colorTransition.a * elapsedTime;
    // };
}
