import Particle, { Props } from '../../Engine/Graphics/Particle';
import Path from './Path';

export default class PathedParticle extends Particle {
    private pathSpeed: number = 0;

    constructor(private path: Path, baseProps: Props) {
        super(baseProps);
        // need to go distance of path in props.lifetime
        this.pathSpeed = path.length / baseProps.lifetime;
    }

    public update = (elapsedTime: number) => {
        this.elapsedTime += elapsedTime;

        const newPos = this.path.travel(this.pathSpeed * elapsedTime);
        // // console.log(newPos);

        // // const { speed, direction } = this.props;
        this.center.x = newPos.x + this.pathSpeed * elapsedTime;
        this.center.y = newPos.y + this.pathSpeed * elapsedTime;

        // const { speed, direction } = this.props;
        // this.center.x += speed * direction.x * elapsedTime;
        // this.center.y += speed * direction.y * elapsedTime;
    };
}
